/* eslint linebreak-style: ["error", "windows"] */

import React, {Component, PropTypes} from 'react';
import {connect} from 'react-redux';
import classnames from 'classnames';

class MainFocus extends Component {
  constructor(props) {
    super(props);

    this.state = ({
      text: this.props.mainFocus.text || '',
      editing: this.props.mainFocus.completed
    });

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    this.handleCheckboxChange = this.handleCheckboxChange.bind(this);
  }

  handleFocus() {
    this.setState({
      text: '',
      editing: true
    });
  }

  handleChange(e) {
    this.setState({
      text: e.target.value
    });
  }

  handleCheckboxChange() {
    this.props.completeMainFocus(!this.props.mainFocus.completed);
  }

  handleSubmit(e) {
    const text = e.target.value.trim();
    const action = {
      text,
      completed: false
    };
    if (e.which === 13) {
      this.props.addMainFocus(action);
      this.setState({
        text,
        editing: false
      });
    }
  }

  render() {
    console.log('this.state', this.state);
    const mainFocusInput = classnames({
      mainFocusInput: true,
      mainFocusEditing: this.state.editing
    });
    const element = (<div className="view">
      <input
        className="toggle"
        type="checkbox"
        checked={this.props.mainFocus.completed}
        onChange={this.handleCheckboxChange}
        />
      <label>label</label>
      <button
        className="destroy"
        />
    </div>);
    return (
      <div className="mainFocusWrapper">
        <input
          type="text"
          className={mainFocusInput}
          value={this.state.text}
          onChange={this.handleChange}
          onKeyDown={this.handleSubmit}
          onFocus={this.handleFocus}
          />
          {this.props.mainFocus.text !== 'What is your main focus for today?' && element}
      </div>
    );
  }
}

MainFocus.propTypes = {
  mainFocus: PropTypes.object.isRequired,
  addMainFocus: PropTypes.func.isRequired,
  completeMainFocus: PropTypes.func.isRequired
};

const mapStateToProps = state => {
  return {
    mainFocus: state.mainFocus
  };
};

export default connect(mapStateToProps)(MainFocus);



